import React, { useEffect } from 'react';
import '../../css/GalaxyButton.css'; // Ensure to create a separate CSS file for the styles

const GalaxyButton = ({handleExplore}) => {
  useEffect(() => {
    const RANDOM = (min, max) => Math.floor(Math.random() * (max - min + 1) + min);
    const PARTICLES = document.querySelectorAll('.star');
    PARTICLES.forEach(P => {
      P.setAttribute('style', `
        --angle: ${RANDOM(0, 360)};
        --duration: ${RANDOM(6, 20)};
        --delay: ${RANDOM(1, 10)};
        --alpha: ${RANDOM(40, 90) / 100};
        --size: ${RANDOM(2, 6)};
        --distance: ${RANDOM(40, 200)};
      `);
    });
  }, []);

  return (
    <div className="galaxy-button pt-3 mt-4">
      <button tabIndex={"-1"} onClick={handleExplore} className='galaxyButton'>
        <span className="spark"></span>
        <span className="backdrop"></span>
        <span className="galaxy__container">
          {[...Array(4)].map((_, i) => (
            <span key={i} className="star star--static"></span>
          ))}
        </span>
        <span className="galaxy">
          <span className="galaxy__ring">
            {[...Array(20)].map((_, i) => (
              <span key={i} className="star"></span>
            ))}
          </span>
        </span>
        <span className="text">EXPLORE</span>
      </button>
      {/* <div className="bodydrop"></div> */}
    </div>
  );
};

export default GalaxyButton;
