import React, { useEffect, useState, useRef } from "react";
import Typewriter from "typewriter-effect/dist/core";
import GalaxyButton from "../components/reusable/GalaxyButton";
import UserForm from "../components/UserForm";
import SunIndex from "../three-src/sunIndex";
import Planets from "../components/Planets";
import sunTexture from "../assets/home_screen_sun.jpg";
import ScrollAnimation from "react-animate-on-scroll";
import SolarSystem from "../three-src/solarSystem";
import kepler from "../assets/Kepler/kepler.jpg";
import kepler_sun from "../assets/Kepler/kepler_sun.jpg";
import MouseTrail from "./MouseTrail";
import Galaxy from "../assets/galaxy.svg";

function Home({ setVisible, setMappingData, mappingData, setIsOpen }) {
  const [step, setStep] = useState(0);
  const [submitted, setSubmitted] = useState(false);
  const [data, setData] = useState({});
  const [exploreClicked, setExploreClicked] = useState(false);
  const [sunAnimate, setSunAnimate] = useState(false);
  const [showSolarSystem, setShowSolarSystem] = useState(false);
  const [planetName, setPlanetName] = useState("");
  const [orbit, setOrbit] = useState(1);
  const [scrollY, setScrollY] = useState(0);
  const [userInteracted, setUserInteracted] = useState(false);

  const audioRef = useRef(new Audio(mappingData?.file));

  useEffect(() => {
    const handleUserInteraction = () => {
      setUserInteracted(true);
      window.removeEventListener('click', handleUserInteraction);
      window.removeEventListener('keydown', handleUserInteraction);
    };

    window.addEventListener('click', handleUserInteraction);
    window.addEventListener('keydown', handleUserInteraction);

    return () => {
      window.removeEventListener('click', handleUserInteraction);
      window.removeEventListener('keydown', handleUserInteraction);
    };
  }, []);

  useEffect(() => {
    audioRef.current.src = mappingData?.file;
    
    if (userInteracted && mappingData.file && (localStorage.getItem("sound") === "true" || localStorage.getItem("sound") === true)) {
      const playAudio = setTimeout(() => {
        audioRef.current.play();
      }, 2000);

      return () => clearTimeout(playAudio); // Cleanup timeout
    } else {
      audioRef.current.pause();
    }
  }, [mappingData, userInteracted]);

  useEffect(() => {
    const typewriter = new Typewriter("#typewriter", {
      strings: [
        "Prepare to float over the galaxy! We are searching for exoplanets, which are worlds that are hidden treasures orbiting far-off stars. Awaiting thousands, are you prepared to jump in? ",
      ],
      autoStart: true,
      loop: true,
      delay: 50,
    });

    return () => {
      typewriter.stop();
    };
  }, []);

  const handleExplore = () => {
    setStep(step + 1);
    setExploreClicked(true);
  };

  const handlePlanetClick = () => {
    setSunAnimate(true);
    setShowSolarSystem(true);
    setTimeout(() => {
      document.querySelector(".solar-system-container").style.display = "block";
      document.querySelector(
        ".solar-system-container"
      ).style.animationPlayState = "running";
    }, 2000);
    setTimeout(() => {
      setVisible(true);
    }, 4000);
  };

  const handleGoBack = () => {
    audioRef.current.pause(); // Stop the audio
    audioRef.current.currentTime = 0; // Reset the audio playback
    setMappingData({});
    setData({});
    setSunAnimate(false);
    setShowSolarSystem(false);
    setVisible(false);
  };

  return (
    <div>
      <div className="h-mx">
        <div
          className={`main-container ${submitted ? "fade-out-background" : ""}`}
        >
          {!submitted && <img src={Galaxy} className="galaxy-planet" />}

          <div
            className={`introduction-text-container d-flex text-align-center align-items-center justify-content-center flex-column ${exploreClicked ? "slide-out" : ""
              }`}
          >
            <h1 className="introduction-text">The Passport To Unusual Worlds</h1>
            <div className="introduction-text-mini mt-3" id="typewriter"></div>
            <GalaxyButton handleExplore={handleExplore} />
          </div>
        </div>
      </div>

      <div className={`second-box flex-column ${exploreClicked ? "slide-in" : ""}`}>
        <div className={`form-container ${submitted ? "slide-out-left" : ""}`}>
          {!submitted ? <UserForm setSubmitted={setSubmitted} userInteracted={userInteracted} step={step} setStep={setStep} /> : ""}
        </div>

        <div className={`rocket-container ${submitted ? "slide-in-right" : ""}`}>
          <MouseTrail />
          {submitted ? (
            <div className="d-flex">
              <div className="scroll-for-more">Scroll Down To Explore More Planets</div>
              <SunIndex
                sunTexturePath={sunTexture}
                uid={"leftSun"}
                sizeFactor={9}
                sunAnimate={sunAnimate}
              />

              <div className="d-flex align-items-center justify-content-end">
                {data && Object.keys(data).length > 0 && (
                  <ScrollAnimation
                    animateIn="fadeInUp"
                    duration={1}
                    offset={50}
                    initiallyVisible={false}
                    style={{ width: "22%" }}
                  >
                    <div className="data-container py-3 px-1">
                      <div className="text-center data-container-head">
                        Know More, {data?.name}
                      </div>
                      <div className="mt-3 mr-2">
                        <ul>
                          <li className="f-poppins">
                            Discovery Year: {data?.discoveryYear}
                          </li>
                          <li className="f-poppins">
                            Planet Mass: {data?.planetMass}
                          </li>
                          <li className="f-poppins">
                            Discovery Method: {data?.discoveryMethod}
                          </li>
                          <li className="f-poppins">
                            Planet Radius: {data?.planetRadius}
                          </li>
                        </ul>
                      </div>
                      <span className="top"></span>
                      <span className="right"></span>
                      <span className="bottom"></span>
                      <span className="left"></span>
                    </div>
                  </ScrollAnimation>
                )}

                <Planets
                  setScrollY={setScrollY}
                  setOrbit={setOrbit}
                  setData={setData}
                  setMappingData={setMappingData}
                  setClickedPlanet={handlePlanetClick}
                  sunAnimate={sunAnimate}
                  setPlanetName={setPlanetName}
                />

                {showSolarSystem && (
                  <>
                    <div className="solar-system-container">
                      <audio src={""} style={{ visibility: "hidden" }} />
                      <SolarSystem
                        sunPath={mappingData.sun ? mappingData.sun : kepler_sun}
                        planetPath={
                          mappingData.planetTexture
                            ? mappingData.planetTexture
                            : kepler
                        }
                        setVisible={setVisible}
                        setIsOpen={setIsOpen}
                        orbit={orbit}
                      />
                      <div className="planetName">
                        {planetName ? planetName : ""}
                      </div>
                    </div>
                    <button className="go-back" onClick={handleGoBack}>
                      <span style={{ fontWeight: "bold" }}>&#8592;</span> &nbsp;
                      Back
                    </button>
                  </>
                )}
              </div>
            </div>
          ) : (
            ""
          )}
        </div>
      </div>
    </div>
  );
}

export default Home;
