import React, { useEffect, useRef } from 'react';
import './MouseTrail.scss'; // Make sure to import your SCSS here

const MouseTrail = () => {
  const x1 = useRef(0);
  const y1 = useRef(0);
  const delay = 1000;

  const dist_to_draw = 50;
  const fsize = ['1.1rem', '1.4rem', '.8rem', '1.7rem'];
  const colors = [
    '#E23636',
    '#F9F3EE',
    '#E1F8DC',
    '#B8AFE6',
    '#AEE1CD',
    '#5EB0E5'
  ];

  const rand = (min, max) => Math.floor(Math.random() * (max - min + 1)) + min;

  const distanceTo = (x1, y1, x2, y2) =>
    Math.sqrt((Math.pow(x2 - x1, 2)) + (Math.pow(y2 - y1, 2)));

  const shouldDraw = (x, y) =>
    (distanceTo(x1.current, y1.current, x, y) >= dist_to_draw);

  const addStr = (x, y) => {
    const str = document.createElement("div");
    str.innerHTML = '&#10022;';
    str.className = 'star';
    str.style.top = `${y + rand(-20, 20)}px`;
    str.style.left = `${x}px`;
    str.style.color = colors[rand(0, colors.length - 1)];
    str.style.fontSize = fsize[rand(0, fsize.length - 1)];

    document.body.appendChild(str);

    const fs = 10 + 5 * parseFloat(getComputedStyle(str).fontSize);
    str.animate({
      translate: `0 ${(y + fs) > window.innerHeight ? window.innerHeight - y : fs}px`,
      opacity: 0,
      transform: `rotateX(${rand(1, 500)}deg) rotateY(${rand(1, 500)}deg)`
    }, {
      duration: delay,
      fill: 'forwards',
    });

    setTimeout(() => {
      str.remove();
    }, delay);
  };

  useEffect(() => {
    const handleMouseMove = (e) => {
      const { clientX, clientY } = e;
      if (shouldDraw(clientX, clientY)) {
        addStr(clientX, clientY);
        x1.current = clientX;
        y1.current = clientY;
      }
    };

    window.addEventListener("mousemove", handleMouseMove);
    
    return () => {
      window.removeEventListener("mousemove", handleMouseMove);
    };
  }, []);

  return (
    <div>
      {/* <h1>Move your mouse around</h1> */}
    </div>
  );
};

export default MouseTrail;