import React, { useEffect, useRef } from "react";
import * as THREE from "three";
import {
  Scene,
  WebGLRenderer,
  PerspectiveCamera,
  LinearSRGBColorSpace,
  ACESFilmicToneMapping,
} from "three";
import { OrbitControls } from "three/addons/controls/OrbitControls.js";
import { Sun } from "./sun";
import { Planet } from "./planet";
import { Starfield } from "./starfield.js";

const SolarSystem = ({ sunPath, planetPath, orbit, setVisible, setIsOpen }) => {
  const mountRef = useRef(null);

  useEffect(() => {
    const size = 0.9; // this is the size of the earth
    const texturePathPlanet = planetPath;

    const planets = [
      {
        orbitSpeed: 0.00048,
        orbitRadius: 10,
        orbitRotationDirection: "clockwise",
        planetSize: orbit === 1 ? size : 0.0,
        planetRotationSpeed: 0.005,
        planetRotationDirection: "counterclockwise",
        planetTexture: texturePathPlanet,
        rimHex: 0xf9cf9f,
      },
      {
        orbitSpeed: 0.00035,
        orbitRadius: 13,
        orbitRotationDirection: "clockwise",
        planetSize: orbit === 2 ? size : 0.0,
        planetRotationSpeed: 0.0005,
        planetRotationDirection: "clockwise",
        planetTexture: planetPath,
        rimHex: 0xb66f1f,
      },
      {
        orbitSpeed: 0.00029,
        orbitRadius: 16,
        orbitRotationDirection: "clockwise",
        planetSize: orbit === 3 ? size : 0.0,
        planetAngle: (-23.4 * Math.PI) / 180,
        planetRotationSpeed: 0.01,
        planetRotationDirection: "clockwise",
        planetTexture: planetPath,
      },
      {
        orbitSpeed: 0.00024,
        orbitRadius: 19,
        orbitRotationDirection: "clockwise",
        planetSize: orbit === 4 ? size : 0.0,
        planetRotationSpeed: 0.01,
        planetRotationDirection: "counterclockwise",
        planetTexture: planetPath,
        rimHex: 0xbc6434,
      },
    ];

    // Set up the scene
    const w = window.innerWidth;
    const h = window.innerHeight;

    const scene = new Scene();
    const camera = new PerspectiveCamera(75, w / h, 0.1, 100);
    const renderer = new WebGLRenderer({ antialias: true, alpha: true });

    // Set camera position
    camera.position.set(
      2 * Math.cos(Math.PI / 6),
      2 * Math.sin(Math.PI / 3),
      24
    );

    const controls = new OrbitControls(camera, renderer.domElement);
    controls.enablePan = false;
    controls.minDistance = 0;
    controls.maxDistance = 60;

    // Add starfield
    const starfield = new Starfield().getStarfield({ num: 2000 });
    scene.add(starfield);

    // Set up renderer properties
    renderer.setSize(w, h);
    renderer.setPixelRatio(window.devicePixelRatio);
    renderer.toneMapping = ACESFilmicToneMapping;
    renderer.outputColorSpace = LinearSRGBColorSpace;

    if (mountRef.current) {
      mountRef.current.appendChild(renderer.domElement);
    }

    const sun = new Sun(sunPath).getSun();
    scene.add(sun);

    // Add planets
    planets.forEach((item) => {
      const planet = new Planet(item).getPlanet();
      scene.add(planet);
    });

    // Handle window resize
    const handleResize = () => {
      const w = window.innerWidth;
      const h = window.innerHeight;
      renderer.setSize(w, h);
      camera.aspect = w / h;
      camera.updateProjectionMatrix();
    };

    window.addEventListener("resize", handleResize);

    // Animation loop
    const animate = () => {
      requestAnimationFrame(animate);
      controls.update();
      renderer.render(scene, camera);
    };

    animate();

    const handleMouseDown = () => {
      setIsOpen(false);
    };

    const handleMouseUp = () => {
      setIsOpen(true);
    };

    window.addEventListener("mousedown", handleMouseDown);
    window.addEventListener("mouseup", handleMouseUp);

    return () => {
      // Clean up on unmount
      window.removeEventListener("resize", handleResize);
      window.removeEventListener("mousedown", handleMouseDown);
      window.removeEventListener("mouseup", handleMouseUp);
      
      if (mountRef.current && renderer.domElement.parentElement === mountRef.current) {
        mountRef.current.removeChild(renderer.domElement);
      }
    };
  }, [sunPath, planetPath, orbit, setVisible]);

  return <div ref={mountRef} style={{ width: "100%", height: "100vh" }} />;
};

export default SolarSystem;
