import React, { useState } from "react";
// import "./Navigation.css";

const Navigation = ({ mappingData, isOpen, setIsOpen }) => {

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className="hamburger-container">
      <div
        className={`hamburger-icon ${isOpen ? "open" : ""}`}
        onClick={toggleMenu}
      >
        <span></span>
        <span></span>
        <span></span>
      </div>
      <div className={`menu ${isOpen ? "menu-open" : ""}`}>
        <ul>
          <li>Orbital Star: <span className="valuePair">{mappingData.orbitalStar}</span></li>
          <li>Orbital Period: <span className="valuePair">{mappingData.orbitalPeriod}</span></li>
          <li>Distance From Star: <span className="valuePair">{mappingData.distanceStar}</span></li>
          <li>Discovery Year: <span className="valuePair">{mappingData.discoverYear}</span></li>
          <li>Planet Mass: <span className="valuePair">{mappingData.planetMass}</span></li>
          <li>Discovery Method: <span className="valuePair">{mappingData.discoveryMethod}</span></li>
          <li>Planet Type: <span className="valuePair">{mappingData.planetType}</span></li>
          <li>Planet Radius: <span className="valuePair">{mappingData.planetRadius}</span></li>
          <li>Orbital Radius: <span className="valuePair">{mappingData.orbitalRadius}</span></li>
          <li>Eccentricity: <span className="valuePair">{mappingData.eccentricity}</span></li>
          <li>Orbital Velocity: <span className="valuePair">{mappingData.orbitalVelocity}</span></li>
        </ul>
      </div>
    </div>
  );
};

export default Navigation;
