import React, { useEffect, useRef } from "react";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import getStarfield from "./getStarfield.js";
import { getFresnelMat } from "./getFresnelMat.js";

const Planet = ({ texturePath }) => {
  const mountRef = useRef(null);

  useEffect(() => {
    const w = window.innerWidth /2;
    const h = window.innerHeight;

    // Scene, Camera, and Renderer
    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(74, w / h, 0.1, 1000);
    camera.position.z = 2;

    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
    renderer.setSize(w, h);
    mountRef.current.appendChild(renderer.domElement);

    // Earth group
    const earthGroup = new THREE.Group();
    earthGroup.rotation.z = 0;
    scene.add(earthGroup);

    // Orbit controls
    const controls = new OrbitControls(camera, renderer.domElement);
    controls.enableZoom = false;
    controls.enablePan = false;

    // Texture Loader
    const loader = new THREE.TextureLoader();
    const geometry = new THREE.IcosahedronGeometry(1.0, 12);
    const material = new THREE.MeshStandardMaterial({
      map: loader.load(texturePath),
    });

    // Earth mesh
    const earthMesh = new THREE.Mesh(geometry, material);
    earthGroup.add(earthMesh);

    // Glow (Fresnel effect)
    const fresnelMat = getFresnelMat({ rimHex: 0xd27d2d, facingHex: 0xb87333 });
    const glowMesh = new THREE.Mesh(geometry, fresnelMat);
    glowMesh.scale.setScalar(1.002);
    earthGroup.add(glowMesh);

    // Stars
    const stars = getStarfield({ numStars: 2000 });
    scene.add(stars);

    // Sunlight
    const sunLight = new THREE.DirectionalLight("0xffffffff");
    sunLight.position.set(-2, -0.5, 1.5);
    scene.add(sunLight);

    // Animation function
    const animate = () => {
      requestAnimationFrame(animate);
      earthMesh.rotation.y += 0.001;
      glowMesh.rotation.y += 0.02;
      renderer.render(scene, camera);
    };  

    animate();

    // Handle window resize
    const handleWindowResize = () => {
      camera.aspect = (window.innerWidth / 4) / (window.innerHeight/2);
      // camera.updateProjectionMatrix();
      // renderer.setSize(window.innerWidth, window.innerHeight);
    };

    window.addEventListener("resize", handleWindowResize);

    // Cleanup on component unmount
    return () => {
      window.removeEventListener("resize", handleWindowResize);
      if (mountRef.current && renderer.domElement.parentElement === mountRef.current) {
        mountRef.current.removeChild(renderer.domElement);
      }
    };
  }, [texturePath]);

  return <div ref={mountRef} />;
};

export default Planet;
