import React, { useEffect, useRef } from "react";
import * as THREE from "three";
import { OrbitControls } from "three/addons/controls/OrbitControls.js";
import { Sun } from "./sun.js"; // Ensure this path is correct
import { Starfield } from "./starfield.js"; // Ensure this path is correct

const SunIndex = ({ sunTexturePath, uid, sunAnimate, renderer }) => {
  const mountRef = useRef(null);

  useEffect(() => {
    const w = window.innerWidth;
    const h = window.innerHeight;

    // Scene, Camera, and Renderer
    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(75, w / h, 0.1, 100);
    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
    const controls = new OrbitControls(camera, renderer.domElement);
    controls.enableZoom = false;
    controls.enablePan = false;
    controls.minDistance = 0;
    controls.maxDistance = 60;

    // Set initial camera position
    camera.position.set(2 * Math.cos(Math.PI / 6), 2 * Math.sin(Math.PI / 3), 8);

    // Starfield
    const starfield = new Starfield().getStarfield({ num: 2000 });
    scene.add(starfield);

    // Sun with texture and unique ID passed from props
    const sun = new Sun(sunTexturePath).getSun();
    scene.add(sun);

    // Renderer settings
    renderer.setSize(w, h);
    renderer.setPixelRatio(window.devicePixelRatio);
    renderer.toneMapping = THREE.ACESFilmicToneMapping;
    renderer.outputColorSpace = THREE.LinearSRGBColorSpace;

    // Attach the renderer to the DOM with unique ID
    if (mountRef.current) {
      mountRef.current.id = `sun-container-${uid}`; // Add unique ID for CSS styling
      mountRef.current.appendChild(renderer.domElement);
    }

    // Handle window resize
    const handleResize = () => {
      const w = window.innerWidth;
      const h = window.innerHeight;
      renderer.setSize(w, h);
      camera.aspect = w / h;
      camera.updateProjectionMatrix();
    };

    window.addEventListener("resize", handleResize);

    // Animation loop
    const animate = () => {
      requestAnimationFrame(animate);
      controls.update();
      renderer.render(scene, camera);
    };

    animate();

    // Cleanup on unmount
    return () => {
      window.removeEventListener("resize", handleResize);
      if (mountRef.current) {
        mountRef.current.removeChild(renderer.domElement);
      }
    };
  }, [sunTexturePath, uid, sunAnimate]); // Re-run effect if the sunTexturePath or uid changes

  return (
    <div ref={mountRef} className={sunAnimate ? "sun-animate" : ""} /> // Add animation class if triggered
  );
};

export default SunIndex;
