import React, { useState, useEffect } from "react";
import astronaut from "../assets/astro.png";
import Typewriter from "typewriter-effect/dist/core"; // Import Typewriter from core

const UserForm = ({ setSubmitted, userInteracted, step, setStep }) => {
  const [name, setName] = useState("");
  const [loading, setLoading] = useState(false);
  const [showButtons, setShowButtons] = useState(true); // New state to control button visibility

  const nextStep = () => {
    if (step === 1 && name.trim() === "") {
      alert("Please enter your name.");
      return;
    }
    setStep(step + 1);
  };

  useEffect(() => {
    try{
      if (userInteracted && step === 1) {
        const audio = new Audio("./audios/Form_1.wav");
        audio.play();
      } else if (userInteracted && step === 2) {
        const audio = new Audio("./audios/Form_2.wav");
        audio.play();
      } else if (userInteracted && step === 3) {
        const audio = new Audio("./audios/Form_3.wav");
        audio.play();
      }
    }
    catch (e){
      console.log(e)
    }
  }, [step, userInteracted]);

  const submitForm = (withSounds) => {
    setLoading(true);
    setShowButtons(false); // Hide buttons when submitting
    localStorage.setItem("name", name);
    localStorage.setItem("sound", withSounds);
    const typewriterThanks = new Typewriter("#typewriter2", {
      autoStart: true,
      loop: false,
      delay: 30,
      deleteSpeed: null,
    });

    // Clear previous text and show new text after submission
    typewriterThanks
      .deleteAll()
      .typeString("Thanks for telling us your preferences.")
      .pauseFor(3000) // Pause for 3 seconds
      .start(); // Start typing and pause

    setTimeout(() => {
      setLoading(false);
      setSubmitted(true); // Trigger the prop function to set state
    }, 3000); // 3-second delay
  };

  return (
    <div className="mt-5">
      <div className="card bg-none text-white">
        <div className="card-body">
          <form>
            {step === 1 && (
              <div className="step">
                <div className="d-flex form-container">
                  <div className="form-content" style={{ width: "65%" }}>
                    <h4>
                      Who is it that I have on the other side of the screen
                      today?
                    </h4>
                    <div className="form-group my-3">
                      <input
                        type="text"
                        className="form-control w-30-imp"
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        placeholder="Your Name"
                      />
                    </div>
                    <button
                      type="button"
                      className="btn btn-primary"
                      onClick={nextStep}
                    >
                      Next
                    </button>
                  </div>
                  <img
                    src={astronaut}
                    alt="Astronaut"
                    className="p-absolute astro"
                  />
                </div>
              </div>
            )}

            {step === 2 && (
              <div className="step">
                <div className="d-flex form-container">
                  <div className="form-content" style={{ width: "90%" }}>
                    <h4 id="typewriter2">
                      Would you like to proceed with the audio playing or not?
                    </h4>
                    <div className="form-group my-3">
                      {showButtons && (
                        <div className="d-flex">
                          <button
                            type="button"
                            className="btn btn-success"
                            onClick={() => submitForm(true)}
                            disabled={loading} // Disable button while loading
                          >
                            With Audio
                          </button>
                          <button
                            type="button"
                            className="btn mx-4 btn-danger"
                            onClick={() => submitForm(false)}
                            disabled={loading} // Disable button while loading
                          >
                            Without Audio
                          </button>
                        </div>
                      )}
                    </div>
                  </div>
                  <img
                    src={astronaut}
                    alt="Astronaut"
                    className="p-absolute astro"
                  />
                </div>
              </div>
            )}
          </form>
        </div>
      </div>
    </div>
  );
};

export default UserForm;
